
import { Vue, Component, Watch, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'
import { v4 } from 'uuid'

import bannerConfig from './banner.vue'

@Component({
  name: 'bannerGroupConfig',
  components: {
    bannerConfig
  }
})
export default class bannerGroupConfig extends Vue {
  @Prop({ type: Object }) value!: { [k: string]: any }

  insideValue: { [k: string]: any } = {
    fieldList: [],
    configData: {
      autoplay: '3000',
      type: '',
      indicatorColor: ''
    }
  }

  @Watch('value', { immediate: true })
  valueHandler() {
    this.insideValue = cloneDeep(this.value)
  }

  addImage() {
    this.insideValue.fieldList.push({
      name: '',
      type: 'banner',
      field: `banner_${v4()}`,
      label: '',
      value: '',
      configData: {
        url: '',
        exampleImage: ''
      },
      ruleList: [],
      events: []
    })
    this.saveConfigChange()
  }

  typeChagne() {
    this.insideValue.value = ''
    this.saveConfigChange()
  }

  deleteImage(index: number) {
    this.insideValue.fieldList.splice(index, 1)
    this.saveConfigChange()
  }

  saveConfigChange() {
    // 
    if (JSON.stringify(this.insideValue) !== JSON.stringify(this.value)) {
      this.$emit('input', this.insideValue)
      this.$emit('change', this.insideValue)
    }
  }
}
