import { Vue, Component } from 'vue-property-decorator'

@Component({
  name: 'mixinsForm'
})
export default class mixinsForm extends Vue {
  loading: boolean = false

  async startSubmit(): Promise<void> {
    try {
      this.loading = true
      const code: number = await (this as any).submit()
      this.loading = false
      if (code === 200) {
        this.$notify.success({ title: '', message: '~' })
        // this.routeBack()
      }
    } catch (error) {
      this.loading = false
    }
  }
}
