
import { Mixins, Component, Ref } from 'vue-property-decorator'

import widgetConfig from '../mixins/widgetConfig'

import ruleList from '../components/ruleList.vue'

@Component({
  name: 'inputConfig',
  components: {
    ruleList
  }
})
export default class inputConfig extends Mixins(widgetConfig) {
  insideValue: { [k: string]: any } = {
    label: '',
    alias: '',
    value: '',
    boolShowDetail: 0,
    boolShowList: 0,
    boolOrderUnique: 0,
    boolOrderApprove: 0,
    boolOrderSearch: 0,
    configData: {
      type: '',
      placeholder: '',
      maxlength: '',
      rows: '',
      disabled: false,
      isHide: false,
      isNeedExample: false,
      exampleTitle: '',
      exampleDescription: '',
      exampleImageUrl: '',
      needCaptcha: false,
      showLogicList: []
    },
    securityType: '',
    ruleList: []
  }
}
