
import { Vue, Component, Watch, Prop } from 'vue-property-decorator'
import { createPromise, isEmpty } from 'plugin/helper'
import { cloneDeep } from 'lodash'

import elementSelect from './elementSelect.vue'
import elementConfig from './elementConfig.vue'

import mobilePreview from './mobilePreview.vue'
import mobilePreviewPages from './mobilePreviewPages.vue'
import mobilePreviewContorl from './mobilePreviewContorl.vue'

import Sortable from 'sortablejs'
import event from '../../event'

interface settingInterface {
  type: 'components' | 'widgets'
  componentIndex: null | number
  widgetIndex: null | number
}

@Component({
  name: 'bindElements',
  components: {
    elementSelect,
    elementConfig,
    mobilePreview,
    mobilePreviewPages,
    mobilePreviewContorl
  }
})
export default class bindElements extends Vue {
  @Prop({ type: Object, default: () => ({}) }) queryRule!: any
  @Prop({ type: Array, default: () => [] }) componentList!: Array<pageComponentsInterface>

  data: Array<pageComponentsInterface> = []

  pageSize: number = 1
  pageIndex: number = 1

  setting: settingInterface = {
    type: 'components',
    componentIndex: null,
    widgetIndex: null
  }

  mobilePreviewLoading: boolean = false

  @Watch('data', { deep: true })
  dataHandler() {
    this.updatePreviewData()

    this.$emit('update:componentList', cloneDeep(this.data))
  }

  init() {
    this.formatComponentList(cloneDeep(this.componentList) || [])
  }

  // 
  validator() {
    const { promise, resolve, reject } = createPromise()

    let billCount: number = 0
    let networkCount: number = 0
    let payTypeCount: number = 0
    let fieldList: Array<pageWidgetsInterface> = []

    this.data.forEach(item => {
      //  billCount
      if (item.type === 'bill' || item.type === 'submitBar') ++billCount
      //  
      if (item.type === 'payType') ++payTypeCount
      if (item.fieldList) fieldList = fieldList.concat(item.fieldList)
    })

    // 
    if (billCount > 1) {
      this.$notify.warning('')
      reject(billCount)
      return promise
    }

    // 
    if (payTypeCount > 1) {
      this.$notify.warning('')
      reject(payTypeCount)
      return promise
    }

    //  field 
    for (let i = 0; i < fieldList.length; i++) {
      const item = fieldList[i]

      const { field } = item
      const { pageIndex } = item.configData

      const filterFieldList = fieldList.filter(
        (item, index) => i !== index && item.field === field && item.configData.pageIndex === pageIndex
      )

      if (!isEmpty(filterFieldList)) {
        this.$notify.warning('')
        reject(filterFieldList)
        return promise
      }

      //  networkCount
      if (item.type === 'network') ++networkCount
    }

    // 
    if (networkCount > 1) {
      this.$notify.warning('')
      reject(networkCount)
      return promise
    }

    resolve(true)
    return promise
  }

  // 
  async result() {
    const data: Array<pageComponentsInterface> = cloneDeep(this.data)

    let serialNo = 1

    data.forEach((item, index) => {
      // 
      if (item.type === 'mail') {
        item.configData.serialNo = serialNo
        item.configData.components.forEach((item: any) => {
          item.configData.serialNo = serialNo
        })
        serialNo++
      }

      item.value = JSON.stringify(item.value)
      item.configData = JSON.stringify(item.configData)
      item.orderNum = index

      // 
      if (item.fieldList) {
        item.fieldList.forEach((item, index) => {
          item.value = JSON.stringify(item.value)
          item.configData = JSON.stringify(item.configData)
          item.orderNum = index

          if (item.ruleList) {
            item.ruleList.forEach((item, index) => (item.orderNum = index))
          }
        })
      }
    })

    return data
  }

  // 
  formatComponentList(componentList: Array<pageComponentsInterface>) {
    let pageSize = 1

    componentList.forEach(item => {
      item.value = JSON.parse(item.value)
      item.configData = JSON.parse(item.configData)
      //  pageIndex 
      if (!item.configData.pageIndex) item.configData.pageIndex = 1

      const { pageIndex } = item.configData
      if (pageIndex > pageSize) pageSize = pageIndex

      if (item.fieldList) {
        item.fieldList.forEach(item => {
          item.value = JSON.parse(item.value)
          item.configData = JSON.parse(item.configData)
          //  pageIndex 
          if (!item.configData.pageIndex) item.configData.pageIndex = 1
        })
      }
    })

    this.pageSize = pageSize
    this.data = componentList

    this.initState()
  }

  // 
  initState() {
    let timeout: number | null = null

    const loop = () => {
      timeout && clearTimeout(timeout)
      timeout = setTimeout(() => {
        if (event.eventQueueMap['updatePreviewData']) {
          this.updatePreviewData()

          this.mobilePreviewLoading = false
        } else {
          loop()
        }
      }, 500)
    }

    loop()
  }

  // 
  insertData(
    type: 'components' | 'widgets',
    data: pageComponentsInterface | pageWidgetsInterface,
    componentIndex: number,
    widgetIndex: number
  ) {
    if (type === 'components') {
      this.data.splice(componentIndex, 0, data as pageComponentsInterface)
    }
    if (type === 'widgets' && this.data[componentIndex]) {
      const fieldList = this.data[componentIndex].fieldList
      if (fieldList) fieldList.splice(widgetIndex, 0, data as pageWidgetsInterface)
      this.data[componentIndex].fieldList = fieldList
    }

    this.updatePreviewData()
  }

  // 
  updatePositionData(type: 'components' | 'widgets', newIndex: number, componentIndex: number, widgetIndex: number) {
    if (type === 'components') {
      this.data.splice(newIndex, 0, this.data.splice(componentIndex, 1)[0])
    }

    if (type === 'widgets' && this.data[componentIndex]) {
      const fieldList = this.data[componentIndex].fieldList
      if (fieldList) fieldList.splice(newIndex, 0, fieldList.splice(widgetIndex, 1)[0])
      this.data[componentIndex].fieldList = fieldList
    }

    this.updatePreviewData()
  }

  // 
  deleteData(type: 'components' | 'widgets', componentIndex: number, widgetIndex: number) {
    if (type === 'components') {
      this.data.splice(componentIndex, 1)
    }

    if (type === 'widgets' && this.data[componentIndex]) {
      const fieldList = this.data[componentIndex].fieldList
      if (fieldList) fieldList.splice(widgetIndex, 1)
      this.data[componentIndex].fieldList = fieldList
    }

    this.updatePreviewData()
  }

  // 
  updateSetting(setting: settingInterface) {
    this.setting = setting
  }

  // 
  handlerPageChange() {
    this.setting.componentIndex = null
    this.setting.widgetIndex = null

    this.updatePreviewData()
  }

  // 
  handlerPageRemove(index: number) {
    const data = this.data.filter(component => {
      return component.configData.pageIndex !== index
    })

    this.data = data
  }

  // 
  updatePreviewData() {
    event.$emit('updatePreviewData', cloneDeep(this.data), this.pageIndex, this.queryRule)
  }

  // 
  createdSortable(el: HTMLElement, options: Sortable.Options) {
    new Sortable(el, options)
  }

  // 
  childrenPageStepUp() {
    this.updatePreviewData()
  }

  created() {
    // 
    event.$on('childrenPageStepUp', this.childrenPageStepUp)
    event.$on('insertData', this.insertData)
    event.$on('deleteData', this.deleteData)
    event.$on('updatePositionData', this.updatePositionData)
    event.$on('updateSetting', this.updateSetting)
    event.$on('createdSortable', this.createdSortable)
  }
}
