import { Vue, Component } from 'vue-property-decorator'
import { isEqualWith } from 'lodash'

@Component({
  name: 'mixinsDetail'
})
export default class mixinsDetail extends Vue {
  componentInit = false
  // 
  routeQuery: { [k: string]: any } = {}

  // 
  init() {
    const query: { [k: string]: any } = this.$route.query

    //   
    const isEqual = isEqualWith(this.routeQuery, query, (value, other) => {
      if (typeof value === 'number' || typeof other === 'number') {
        return String(value) === String(other)
      }
    })

    // 
    if (!isEqual) {
      this.routeQuery = query
      ;(this as any).getDetail()
    }
  }

  mounted() {
    this.$nextTick(() => {
      !this.componentInit && this.init()
    })
  }

  activated() {
    this.componentInit = true
    this.init()
  }
}
