/**
 * iframe 
 */
let instance: IframeEvent | null = null

class IframeEvent {
  constructor() {
    if (!instance) instance = this
    return instance
  }

  eventQueueMap: { [k: string]: Function } = {}

  $emit(type: string, ...args: Array<any>) {
    if (this.eventQueueMap[type]) {
      this.eventQueueMap[type](...args)
    }
  }

  $on(type: string, fun: Function) {
    this.eventQueueMap[type] = fun
  }

  $off(type: string) {
    if (this.eventQueueMap[type]) {
      delete this.eventQueueMap[type]
    }
  }
}

const evnet = new IframeEvent()

window.$emit = evnet.$emit.bind(evnet)

window.$on = evnet.$on.bind(evnet)

window.$off = evnet.$off.bind(evnet)

export default evnet
