
import { Mixins, Component } from 'vue-property-decorator'

import componentConfig from '../mixins/componentConfig'

@Component({
  name: 'formConfig'
})
export default class formConfig extends Mixins(componentConfig) {
  insideValue: { [k: string]: any } = {
    configData: {
      title: '',
      labelPosition: 'left'
    }
  }
}
