
import { Vue, Component, Watch, Prop, Ref } from 'vue-property-decorator'
import { ElForm } from 'element-ui/types/form'
import { isEmpty } from 'plugin/helper'
import { cloneDeep } from 'lodash'

@Component({
  name: 'showLogicList'
})
export default class showLogicList extends Vue {
  @Ref('comDialog') comDialogRef!: any
  @Ref('form') formRefs!: Array<ElForm>

  @Prop({ type: Array, default: () => [] }) value!: Array<any>
  @Prop({ type: Array, default: () => [] }) pageConfig!: Array<pageComponentsInterface>

  insideValue: Array<any> = []

  rules = {
    bind: [{ required: true, message: '', trigger: 'change' }],
    value: [{ required: true, message: '', trigger: 'change' }]
  }

  // 
  get widgetsOptions(): Array<any> {
    const widgetsOptions: Array<any> = []
    // 
    const widgetsTypeList: Array<string> = ['select', 'radio']

    const forms = this.pageConfig.filter(item => item.type === 'form')

    forms.forEach(item => {
      if (item.fieldList) {
        item.fieldList.forEach(item => {
          if (widgetsTypeList.includes(item.type) && item.field) {
            widgetsOptions.push({ label: item.label, value: item.field })
          }
        })
      }
    })

    return widgetsOptions
  }

  @Watch('value', { immediate: true })
  valueHandler() {
    const value = cloneDeep(this.value)

    if (isEmpty(value)) {
      this.insideValue = [{ bind: '', value: '' }]
    } else {
      this.insideValue = value
    }
  }

  // 
  valueOptions(key: string): Array<string> {
    let valueOptions: Array<string> = []

    // 
    const widgetsTypeList: Array<string> = ['select', 'radio']

    const forms = this.pageConfig.filter(item => item.type === 'form')

    forms.forEach(item => {
      if (item.fieldList) {
        item.fieldList.forEach(item => {
          if (widgetsTypeList.includes(item.type) && item.field === key) {
            const { configData } = item
            valueOptions = configData ? configData.option || configData.options || [] : []
          }
        })
      }
    })

    return valueOptions
  }

  //  
  addShowLogic() {
    this.insideValue.push({ bind: '', value: '' })
  }

  deleteShowLogic(index: number) {
    this.insideValue.splice(index, 1)
  }

  init() {
    this.comDialogRef.open()
  }

  async beforeDetermine(close: Function, cancelLoading: Function) {
    const promiseQueue: Array<Promise<any>> = []

    this.formRefs.forEach(formRef => {
      promiseQueue.push(formRef.validate())
    })

    try {
      await Promise.all(promiseQueue)
      this.$emit('input', cloneDeep(this.insideValue))
      this.$emit('change')
      close()
      cancelLoading()
    } catch (error) {
      cancelLoading()
    }
  }
}
