
import { Mixins, Component, Watch, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import componentConfig from '../mixins/componentConfig'

@Component({
  name: 'cardConfig',
  components: {}
})
export default class cardConfig extends Mixins(componentConfig) {
  @Prop({ type: Number }) pageSize!: number

  insideValue: { [k: string]: any } = {
    configData: {
      title: '',
      placeholder: '',
      color: '',
      carType: '',
      type: '',
      iconImage: '',
      toPageIndex: ''
    }
  }

  deleteImage() {
    this.insideValue.configData.iconImage = ''
    this.saveConfigChange()
  }
}
