
import { Mixins, Component } from 'vue-property-decorator'

import widgetConfig from '../mixins/widgetConfig'

import ruleList from '../components/ruleList.vue'

@Component({
  name: 'inputScanConfig',
  components: {
    ruleList
  }
})
export default class inputScanConfig extends Mixins(widgetConfig) {
  insideValue: { [k: string]: any } = {
    label: '',
    alias: '',
    value: '',
    boolShowDetail: 0,
    boolShowList: 0,
    boolOrderUnique: 0,
    boolOrderApprove: 0,
    boolOrderSearch: 0,
    configData: {
      placeholder: '',
      maxlength: '',
      readonly: false,
      disabled: false,
      isHide: false,
      showLogicList: []
    },
    securityType: '',
    ruleList: []
  }
}
