
import { Mixins, Component, Watch, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import componentConfig from '../mixins/componentConfig'

@Component({
  name: 'buttonConfig'
})
export default class buttonConfig extends Mixins(componentConfig) {
  @Prop({ type: Number }) pageSize!: number
  @Prop({ type: Number }) pageIndex!: number

  insideValue: { [k: string]: any } = {
    configData: {
      title: '',
      toPageIndex: '',
      color: '#409EFF',
      type: 'created',
      size: 'normal',
      round: false,
      validateBeforeNext: true
    }
  }

  get pageSizeOptions() {
    const pageSizeOptions: Array<number> = []

    for (let i = 1; i <= this.pageSize; i++) {
      if (i !== this.pageIndex) pageSizeOptions.push(i)
    }

    return pageSizeOptions
  }
}
