
import { Mixins, Component, Watch, Ref } from 'vue-property-decorator'

import widgetConfig from '../mixins/widgetConfig'

import ruleList from '../components/ruleList.vue'
import selectOptionControl from '../components/selectOptionControl.vue'

@Component({
  name: 'selectConfig',
  components: {
    ruleList,
    selectOptionControl
  }
})
export default class selectConfig extends Mixins(widgetConfig) {
  @Ref('selectOptionControl') selectOptionControlRef!: selectOptionControl

  singleDefaultValue: string = ''

  insideValue: { [k: string]: any } = {
    label: '',
    alias: '',
    value: [],
    boolShowDetail: 0,
    boolShowList: 0,
    boolOrderUnique: 0,
    boolOrderApprove: 0,
    boolOrderSearch: 0,
    configData: {
      label: '',
      type: '',
      placeholder: '',
      option: [],
      disabled: false,
      isHide: false
    },
    securityType: '',
    ruleList: [],
    evnetList: []
  }

  @Watch('insideValue.value', { immediate: true })
  defaultValueHandler() {
    if (typeof this.insideValue.value === 'string') {
      this.singleDefaultValue = this.insideValue.value
    } else {
      this.singleDefaultValue = this.insideValue.value[0] || ''
    }
  }

  typeChagne() {
    this.insideValue.value = ''
    this.saveConfigChange()
  }

  defaultValueChange() {
    this.insideValue.value = [this.singleDefaultValue]
    this.saveConfigChange()
  }

  openSelectOptionControl() {
    this.selectOptionControlRef.init()
  }
}
