
import { Mixins, Component, Watch, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import componentConfig from '../mixins/componentConfig'

@Component({
  name: 'stepsConfig'
})
export default class stepsConfig extends Mixins(componentConfig) {
  insideValue: { [k: string]: any } = {
    configData: {
      direction: '',
      steps: []
    }
  }

  stepsDirectionOptions = [
    { label: '', value: 'horizontal' },
    { label: '', value: 'vertical' }
  ]
}
