
import { Vue, Component, Watch, Prop } from 'vue-property-decorator'

import draggable from 'vuedraggable'

@Component({
  name: 'ruleList',
  components: {
    draggable
  }
})
export default class ruleList extends Vue {
  @Prop({ type: Array, required: true }) value!: Array<any>

  // 
  @Prop({ type: Boolean, default: false }) disabledChange!: boolean

  insideValue: Array<any> = []

  @Watch('value', { immediate: true })
  valueHandler() {
    this.insideValue = this.value
  }

  // Detail see : https://github.com/RubaXa/Sortable/issues/1012
  setData(dataTransfer: any) {
    dataTransfer.setData('Text', '')
  }

  addRule() {
    this.insideValue.push({ type: 'require', failMsg: '', content: '' })
    this.ruleListChange()
  }

  deleteRule(index: number) {
    this.insideValue.splice(index, 1)
    this.ruleListChange()
  }

  ruleListChange() {
    this.$emit('input', this.insideValue)
    this.$emit('change', this.insideValue)
  }
}
