import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import showLogicList from '../components/showLogicList.vue'

@Component({
  name: 'componentConfig',
  components: {
    showLogicList
  }
})
export default class componentConfig extends Vue {
  @Ref('showLogicList') showLogicListRef!: any

  @Prop({ type: Object }) value!: any

  // 
  @Prop({ type: Array, default: () => [] }) pageConfig!: Array<pageComponentsInterface>

  insideValue: any

  @Watch('value', { immediate: true })
  valueHandler() {
    this.insideValue = cloneDeep(this.value)
  }

  saveConfigChange() {
    // 
    if (JSON.stringify(this.insideValue) !== JSON.stringify(this.value)) {
      this.$emit('input', this.insideValue)
      this.$emit('change', this.insideValue)
    }
  }

  openBindShowLogic() {
    this.showLogicListRef && this.showLogicListRef.init()
  }
}
