
import { Vue, Component, Prop, Ref } from 'vue-property-decorator'
import { isEmpty } from 'plugin/helper'
import { cloneDeep } from 'lodash'

interface option {
  label: string
  value: string
  remarkMaxlength?: string
  isRemark?: boolean
  remarkIsRequire?: boolean
  remarkPlaceholder?: string
  remarkType?: string
}

@Component({
  name: 'selectOptionControl'
})
export default class selectOptionControl extends Vue {
  @Ref('comDialog') comDialogRef!: any
  @Ref('addOptionDialog') addOptionDialogRef!: any

  @Prop({ type: Array }) value!: Array<string | option>

  insideValue: Array<option> = []

  textarea: string = ''

  operateIndex: null | number = null

  get isHasOtherOption() {
    const res = this.insideValue.filter((item) => item.value === '')[0]

    return !!res
  }

  get isReadySettingOption() {
    return typeof this.operateIndex === 'number' && this.insideValue[this.operateIndex]
  }

  init() {
    this.operateIndex = null

    this.formatInsideValue()

    this.comDialogRef.open()
  }

  formatInsideValue() {
    const insideValue: Array<option> = []

    this.value.forEach((item) => {
      if (typeof item === 'string') {
        insideValue.push({
          label: item,
          value: item,
          isRemark: false,
          remarkIsRequire: false
        })
      } else {
        insideValue.push(item)
      }
    })

    this.insideValue = insideValue
  }

  // 
  inputBlurHandler(option: any) {
    const res = this.insideValue.filter((item) => item.value === option.value)

    if (res.length > 1) {
      option.value = ''
      this.$message.warning('~')
    }
  }

  // 
  openAddOption() {
    this.textarea = this.insideValue.map((item) => item.value).join('\n')

    this.addOptionDialogRef.open()
  }

  // 
  baseAddOption(value: string, isRemark: boolean = false) {
    //  100
    const _value = value.replace(/\s+/g, '').substring(0, 100)

    const res = this.insideValue.filter((item) => item.value === _value)[0]

    if (!res && _value) {
      this.insideValue.push({
        label: _value,
        value: _value,
        isRemark: isRemark,
        remarkIsRequire: false
      })
    }
  }

  // 
  addOption(close: Function, cancelLoading: Function) {
    const tokens = this.textarea.split(/\n/)

    let i = this.insideValue.length

    // 
    while (i--) {
      if (!tokens.includes(this.insideValue[i].value)) {
        this.insideValue.splice(i, 1)
      }
    }

    // 
    tokens.forEach((token) => {
      this.baseAddOption(token, false)
    })

    // 
    this.insideValue.sort((prev, next) => tokens.indexOf(prev.value) - tokens.indexOf(next.value))

    this.textarea = ''

    cancelLoading()

    close()
  }

  // 
  addOtherOption() {
    this.baseAddOption('', true)
  }

  // 
  deleteOption(index: number) {
    this.insideValue.splice(index, 1)
  }

  // 
  settingOption(index: number) {
    this.operateIndex = index
  }

  beforeDetermine(close: Function, cancelLoading: Function) {
    //  label  label  value 
    this.insideValue.forEach((item, index) => (this.insideValue[index].label = item.value))

    // value  label  
    const res = this.insideValue.filter((item) => !item.value || !item.label)

    if (!isEmpty(res)) {
      this.$message.warning('~')
      cancelLoading()
      return false
    }

    this.$emit('input', cloneDeep(this.insideValue))
    this.$emit('change')
    cancelLoading()
    close()
  }
}
