
import { Mixins, Component } from 'vue-property-decorator'

import widgetConfig from '../mixins/widgetConfig'

import ruleList from '../components/ruleList.vue'

@Component({
  name: 'dateConfig',
  components: {
    ruleList
  }
})
export default class dateConfig extends Mixins(widgetConfig) {
  insideValue: { [k: string]: any } = {
    label: '',
    alias: '',
    value: '',
    boolShowDetail: 0,
    boolShowList: 0,
    boolOrderUnique: 0,
    boolOrderApprove: 0,
    boolOrderSearch: 0,
    configData: {
      type: '',
      placeholder: '',
      minDate: '2010-01-01',
      maxDate: '2030-01-01',
      disabled: false,
      isHide: false,
      showLogicList: []
    },
    ruleList: []
  }

  get dateRange() {
    return [this.insideValue.configData.minDate, this.insideValue.configData.maxDate]
  }

  set dateRange(value: Array<string>) {
    this.insideValue.configData.minDate = value ? value[0] || '' : ''
    this.insideValue.configData.maxDate = value ? value[1] || '' : ''
  }
}
