
import { Mixins, Component, Watch, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import componentConfig from '../mixins/componentConfig'

@Component({
  name: 'headerConfig'
})
export default class headerConfig extends Mixins(componentConfig) {
  insideValue: { [k: string]: any } = {
    configData: {
      title: '',
      type: '',
      tags: [],
      areaShow: true
    }
  }

  headerTypeOptions = [
    { label: '', value: 'primary' },
    { label: '', value: 'info' },
    { label: '', value: 'darker' },
    { label: '', value: 'light' }
  ]
}
