
import { Vue, Component, Prop, Watch } from 'vue-property-decorator'

@Component({
  name: 'mobilePreviewPages'
})
export default class mobilePreviewPages extends Vue {
  @Prop({ type: Number, default: 1 }) size!: number
  @Prop({ type: Number, default: 5 }) maxSize!: number

  @Prop({ type: Number, required: true }) value!: number

  insideValue: string = String(this.value)

  insideSize: number = Number(this.size)

  @Watch('value')
  valueHandler() {
    this.insideValue = String(this.value)
  }

  @Watch('size')
  sizeHandler() {
    this.insideSize = Number(this.size)
  }

  //  
  handleTabsAdd() {
    // 
    if (this.insideSize === this.maxSize) return

    this.insideSize = ++this.insideSize

    this.insideValue = String(this.insideSize)

    this.$emit('update:size', this.insideSize)

    this.tabClick()
  }

  handleTabsRemove(index: string) {
    this.insideSize = --this.insideSize

    this.insideValue = String(this.insideSize)

    this.$emit('update:size', this.insideSize)

    this.tabClick()

    this.$emit('page-remove', Number(index))
  }

  tabClick() {
    this.$emit('input', Number(this.insideValue))
    this.$emit('page-change', Number(this.insideValue))
  }
}
