
import { Vue, Component, Prop } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'

import { pageComponents, baseComponents, commonComponents, baseWidgets, commonWidgets } from '../../dict'

import elementGroup from './elementGroup.vue'

@Component({
  name: 'elementSelect',
  components: {
    elementGroup
  }
})
export default class elementSelect extends Vue {
  @Prop({ type: Number, default: 1 }) pageIndex!: number

  pageComponents: Array<pageComponentsInterface> = cloneDeep(pageComponents)
  baseComponents: Array<pageComponentsInterface> = cloneDeep(baseComponents)
  commonComponents: Array<pageComponentsInterface> = cloneDeep(commonComponents)

  baseWidgets: Array<pageWidgetsInterface> = cloneDeep(baseWidgets)
  commonWidgets: Array<pageWidgetsInterface> = cloneDeep(commonWidgets)
}
