
import { Component, Mixins, Ref } from 'vue-property-decorator'
import { createPromise } from 'plugin/helper'

import bindElementsViews from './views/bind-elements/index.vue'

import detail from 'mixins/detail'
import form from 'mixins/form'

@Component({
  name: 'pagesSetting',
  components: {
    bindElementsViews
  }
})
export default class pagesSetting extends Mixins(form, detail) {
  @Ref('bindElementsViews') bindElementsViewsRef!: bindElementsViews

  acitve: string = 'bind-elements'

  menu = [
    {
      title: '',
      icon: 'icon-caidan',
      value: 'bind-elements'
    }
  ]

  componentList: Array<pageComponentsInterface> = []
  queryRule: any = {}

  changeActive(acitve: string) {
    this.acitve = acitve
  }

  // 
  async getDetail() {
    const { id } = this.$route.query

    if (!id) return

    const { result } = await this.$page.detail({ id })

    if (result) {
      this.componentList = result.componentList || []
      this.queryRule = result.queryRule || {}
    }

    // 
    this.$nextTick(() => {
      this.bindElementsViewsRef && this.bindElementsViewsRef.init()
    })
  }

  // 
  submit() {
    const { promise, resolve, reject } = createPromise()

    this.$nextTick(async () => {
      // 
      try {
        await this.bindElementsViewsRef.validator()
      } catch (error) {
        this.acitve = 'bind-elements'
        reject(error)
        return
      }

      try {
        const componentList = await this.bindElementsViewsRef.result()

        let blob = new Blob([JSON.stringify(componentList)])
        let link = document.createElement('a')
        link.href = URL.createObjectURL(blob)
        link.download = 'JSON.json'
        link.click()

        resolve(200)
      } catch (error) {
        reject(error)
      }
    })

    return promise
  }
}
