
import { Vue, Component, Watch, Prop } from 'vue-property-decorator'
import { components } from '../../config'

interface settingInterface {
  type: 'components' | 'widgets'
  componentIndex: null | number
  widgetIndex: null | number
}

@Component({
  name: 'elementConfig',
  components: components()
})
export default class elementConfig extends Vue {
  @Prop({ type: Array }) value!: Array<pageComponentsInterface>
  @Prop({ type: Object }) setting!: settingInterface
  @Prop({ type: Number }) pageSize!: number
  @Prop({ type: Number }) pageIndex!: number

  insideValue: Array<pageComponentsInterface> = this.value

  get type() {
    return this.setting.type
  }

  get componentIndex() {
    return this.setting.componentIndex
  }

  get widgetIndex() {
    return this.setting.widgetIndex
  }

  get isShowElementConfig(): boolean {
    if (this.type === 'components') {
      return this.componentIndex !== null
    }

    if (this.type === 'widgets') {
      return this.componentIndex !== null && this.widgetIndex !== null
    }

    return false
  }

  @Watch('value')
  valueHandler() {
    this.insideValue = this.value
  }

  @Watch('insideValue', { deep: true })
  insideValueHandler() {
    this.$emit('input', this.insideValue)
  }

  componentConfigIsRegister(componentType: string) {
    if (this.type === 'components' && this.componentIndex !== null && this.insideValue[this.componentIndex]) {
      return this.insideValue[this.componentIndex].type === componentType
    }

    return false
  }

  widgetConfigIsRegister(componentType: string, widgetType: string) {
    if (
      this.type === 'widgets' &&
      this.componentIndex !== null &&
      this.widgetIndex !== null &&
      this.insideValue[this.componentIndex]
    ) {
      if (this.insideValue[this.componentIndex].type !== componentType) {
        return false
      }

      const fieldList = this.insideValue[this.componentIndex].fieldList

      if (fieldList && fieldList[this.widgetIndex]) {
        return fieldList[this.widgetIndex].type === widgetType
      }
    }

    return false
  }
}
