
import { Vue, Component } from 'vue-property-decorator'

@Component({
  name: 'mobilePreviewContorl'
})
export default class mobilePreviewContorl extends Vue {
  zoom = 100

  max = 150
  min = 70

  // 
  zoomEnlarge() {
    if (this.zoom === this.max) return

    this.zoom += 5

    this.setPreviewStyle(this.zoom)
  }

  // 
  zoomNarrow() {
    if (this.zoom === this.min) return

    this.zoom -= 5

    this.setPreviewStyle(this.zoom)
  }

  setPreviewStyle(proportion: number) {
    const el = document.getElementById('mobile-preview__wrapper')

    if (el) {
      el.style.transform = `scale(${proportion / 100})`
    }
  }
}
